import { StatusBadge } from "./StatusBadge";

const VACCINE_CODE = {
  212: "",
  207: "",
  208: "", // 30 mcg/0.3 mL ages 12+
  217: "", // 30 mcg/0.3 mL for ages 12+
  218: "", // 10 mcg/0.2 mL for ages 5 yrs to < 12 yrs
  219: "", //  3 mcg/0.2 mL for ages 2 yrs to < 5 yrs
  210: "",
  211: "Novavax",
};

function getVaccineName(code) {
  if (code in VACCINE_CODE) {
    return VACCINE_CODE[code];
  } else {
    return ` (: ${code})`;
  }
}

export function ImmunationHistory({
  number,
  datetime,
  vaccineCode,
  status,
  lotNumber,
  performer,
}) {
  return (
    <div className="card mb-2">
      <div className="card-header">{number}</div>
      <ul className="list-group list-group-flush">
        <li className="list-group-item">
          <strong></strong> {datetime}
        </li>
        <li className="list-group-item">
          <strong></strong> {getVaccineName(vaccineCode)}
        </li>
        <li className="list-group-item">
          <strong></strong> {lotNumber}
        </li>
        <li className="list-group-item">
          <strong></strong> <StatusBadge status={status} />
        </li>
      </ul>
    </div>
  );
}
