export function StatusBadge(props) {
  switch (props.status) {
    case "completed":
      return <span className="badge bg-success"></span>;
    case "entered-in-error":
      return <span className="badge bg-danger"></span>;
    case "not-done":
      return <span className="badge bg-danger"></span>;
    default:
      return <span className="badge bg-secondary"></span>;
  }
}
