import { useSHCDecode } from "./module/UseSHCDecode";
import QrReader from "react-qr-reader";
import { ReadResults } from "./components/ReadResults";

function App() {
  const { setQrcodeText, decodedData, isWaiting, isLoading, isValid } =
    useSHCDecode();

  const handleQR = (data) => {
    if (data !== null) {
      setQrcodeText(data);
    }
  };
  const handleError = (err) => {
    console.error(err);
  };

  return (
    <div className="container mt-4">
      <h1></h1>
      <p></p>
      <strong></strong>
      <ul>
        <li>
          
        </li>
        <li>
          
        </li>
        <li>
          
        </li>
        <li>
          Javascriptgithub
          pages
        </li>
      </ul>
      <div className="row">
        <div className="col-lg-5">
          <QrReader
            delay={300}
            onError={handleError}
            onScan={handleQR}
            style={{ width: "100%" }}
          />
        </div>
        <div className="col-lg-7">
          <ReadResults
            isWaiting={isWaiting}
            isLoading={isLoading}
            isValid={isValid}
            data={decodedData}
          />
        </div>
      </div>
    </div>
  );
}

export default App;
