export function VerifyStatus(props) {
  if (props.status) {
    return (
      <div className="alert alert-success" role="alert">
        <strong>OK</strong> 
      </div>
    );
  } else {
    return (
      <div className="alert alert-danger" role="alert">
        <strong></strong>
        QR
      </div>
    );
  }
}
